<template>
  <div id="app">
    <img src="../../images/shuvi-logo.png" width="400px">
    <h2>Button </h2>
    <div>
      <sv-button @click="handleClick"></sv-button>
      <sv-button @click="handleClick" type="info"></sv-button>
      <sv-button @click="handleClick" type="success"></sv-button>
      <sv-button @click="handleClick" type="warning"></sv-button>
      <sv-button @click="handleClick" type="danger"></sv-button>
      <br>
      <br>
      <sv-button @click="handleClick" type="info" plain></sv-button>
      <sv-button @click="handleClick" type="success" plain></sv-button>
      <sv-button @click="handleClick" type="warning" plain></sv-button>
      <sv-button @click="handleClick" type="danger" plain></sv-button>
      <br>
      <br>
      <sv-button @click="handleClick" size="small"></sv-button>
      <sv-button @click="handleClick" size="mini"></sv-button>
    </div>
    <h2>Tag </h2>
    <div>
      <sv-tag></sv-tag>
      <sv-tag :count="10"></sv-tag>
      <sv-tag type="green"></sv-tag>
      <sv-tag type="red" :count="8"></sv-tag>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },

  mounted() {
    this.$info('shuvi');
  },

  methods: {
    handleClick() {
      this.$message('click!');
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



